import React from 'react';
import {Link} from 'react-scroll'

function Header() {
    return (
        <div id="main">
            <div className="headerhead">
                <div id="home">
                    <h2 className="h2">Hey! i'm Tosin </h2>
                    <h1 className="h1"><span>Front End Developer</span></h1>
                    <p>Build your website with a professional touch</p>
                    <div className="col">
                         <a className="btn"><Link to= "portfolio" smooth={true} duration={1000}>View Work</Link></a>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header
