import React, {useState} from 'react';
import {Link} from 'react-scroll'

function Navbar() {

    const [click, setClick] = useState('close');

    return (
        <nav>
            <div className="menu-btn" onClick={() => setClick(click === 'open' ? 'close' : 'open')}>
                <i className={click} id="i"></i>
                <i className={click} id="i"></i>
                <i className={click} id="i"></i>
            </div>
            <ul className={click ==='open' ? 'menu activate' : 'menu'}>
                <li><Link to= "home" smooth={true} duration={1000}>HOME</Link></li>
                <li><Link to= "about" smooth={true} duration={1000}>ABOUT ME</Link></li>
                <li><Link to= "portfolio" smooth={true} duration={1000}>PORTFOLIO</Link></li>
                <li><Link to= "contacts" smooth={true} duration={1000}>CONTACTS</Link></li>
            </ul>
        </nav>
    )
}

export default Navbar;